.notificationList {
    padding: 20px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    max-height: 70vh; /* Chiếm 90% chiều cao của viewport */
    overflow-y: auto; /* Cho phép cuộn dọc */
    scrollbar-width: thin; /* Đối với Firefox */
    scrollbar-color: #ccc transparent; /* Đối với Firefox */
    margin-bottom: 10px;
}

/* Ẩn thanh cuộn cho Chrome, Safari và Edge */
.notificationListTab::-webkit-scrollbar {
    width: 0; /* Ẩn thanh cuộn */
    background: #ccc; /* Nền trong suốt */
}



